@use 'reset';

$violet : #7469F0;

@mixin flexRow {
    display :flex;
    flex-direction: row;
}
@mixin flexColumn {
    display :flex;
    flex-direction: column;
}

.wrap {
    width: 1280px;
    margin: 30px auto;
}

.login {
    width: 1280px;
    @include flexRow();
    justify-content: flex-end;
    // 로그인 / 회원가입 우측 상단으로 보내려고함
}

.login > a {    
    margin-right: 10px;
}

.login > a:hover {
    color:$violet;
}

.logo {
    margin: 15px 0 0 25px;
    width: 150px;
    display: inline-block;
}

.logo > h1 {
    width: 150px;
    font-size: 30px;
    color: $violet;
    font-weight: bold;
}

// 메뉴
.nav {
    margin: 15px 0 35px 110px;
    width: 800px;
    height: 25px;
    display: inline-block;
}

.nav > ul {
    width: 800px;
    @include flexRow();
    text-align: center;
    font-size: 20px;
}

.nav > ul > li {
    width: 180px;
    margin-left: 50px;
}

.nav > ul > li > a:hover {
    // width: 150px;
    color: $violet;
}

.nav > ul > li:hover > .navSub {
    display: block;
} 

// 서브메뉴

.nav .navSub {
    display: none;
    width: 120px;
    height: 165px;
    font-size: 16px;
    margin : 20px auto;
    position: relative;
    background-color: rgba(245,243,243,0.5);
    z-index: 9998;
}

.nav .navSub > li {
    width: 120px;
    height: 40px;
    margin-top: 20px;
    position: relative;
}

.nav .navSub > li > a {
    position: absolute;
    width: 75px;
    height: 20px;
    z-index: 9999;
    top: 10px;
    right: 20px;
    text-align: center;
}

.nav .navSub > li > a:hover {
    color: $violet;
}

// 슬라이드

.mainSlider > .sliderWrap {
    width: 1280px;
    height: 360px;
    overflow: hidden;
} 

.mainSlider > .sliderWrap > .sliderImg {
    width: 3840px;
    animation: sliderLoop 6s infinite ;
}
.mainSlider > .sliderWrap > .sliderImg > img {
    width: 1280px;
}

@keyframes sliderLoop {
    0% {
        transform: translateX(0px);
    }
    50% {
        transform: translateX(-1280px);
    }
    100% {
        transform: translateX(-2560px);
    }
}

// 기온 추천 코너
.temperatureRecommendation > .recommendationWrap{
    width: 1280px;
    height: 300px;
    @include flexRow();
}
.temperatureRecommendation h2 {
    width: 225px;
    color: $violet;
    font-size: 24px;
    margin: 150px auto;
} 

.temperatureRecommendation .recommendationPlace {
    width: 520px;
    margin: 0 auto;
    @include flexRow();
}
.temperatureRecommendation button {
    width: 150px;
    height: 45px;
    font-size: bold;
    border: 1px solid $violet;
    border-radius: 20px;
    background-color: $violet;
    color: #ffffff;
    margin-top: 200px;
}

// 베스트 상품 코너
.mainProduct > .productWrap {
    width: 1280px;
    height: 500px;
}

.mainProduct > .productWrap > h2 {
    width: 200px;
    height: 30px;
    margin: 55px auto;
}

.mainProduct > .productWrap > .productPlace {
    margin: 70px auto;
    @include flexRow();
    justify-content: center;
    justify-content: space-around
}

// 메인 페이지 상품카드
.productWrap {
    width: 250px;
    height: 310px;
    font-size: 18px;
    text-align: center;
}

.productWrap > img {
    width: 200px;
    height: 180px;
}

.productWrap > .productName {
    width: 125px;
    height: 30px;
    margin: 20px auto;
}

.productWrap > .productPrice {
    width: 115px;
    height: 15px;
    margin: 20px auto;
}




// footer
.mainFooter {
    width: 100vw;
    height: 150px;
    background-color: #F5F3F3;
}
.mainFooter > .footerWrap {
    width: 1280px;
}
